import React from 'react'
import {AppBar,Divider, Toolbar, Typography, Avatar, Box} from '@mui/material';
import { makeStyles } from '@mui/styles';
import ListAatar from '../components/Navbar/ListAvatar'




const useStyles = makeStyles((theme) => ({
  toolbar: {
    display: { xs: 'none', md: 'flex' },
  },
  logoLg: {
    display: "none",
    color: "#FF3358",
    height:30,
    flexGrow: 1 ,
    [theme.breakpoints.up("sm")]: {
      display: "block",
    },
  },
  logoText: {
    fontFamily: "Arial, sans-serif",
    fontSize: "20px",
    fontWeight: "bold",
    color: "#0069FF",
    textAlign: "right",
    textShadow: "2px 2px 4px rgba(0, 0, 0, 0.2)", // Optional: Adds a subtle shadow
  },
}));

function Navabr() {
 
  const classes = useStyles();
  return (
    <AppBar  sx={{  position: 'static',color:"inherit" }}>
      <div>
      <Toolbar sx={{display: { xs: 'none', md: 'flex' }, mr: 1}}>
        <Box style={{flexGrow:1}}>
        
        <img src="https://benobe.ir/theme/np_site/portal/assets/images/benobe/benobe.svg"
         alt="logo"   />
        </Box>
        <Typography variant="h5" className={classes.logoText}
        sx={{marginLeft:30}} > پرونده الکترونیک بیمار  </Typography>
 
        <Box sx={{display: { xs: 'none', md: 'flex' },alignItems:"center", mr: 1}}>
        <Typography variant="h6" sx={{margin:3}} > دکتر {" تست"}  </Typography>
        
     
        <ListAatar/>
        </Box>
        
        
      </Toolbar>
      </div>
      <Divider light />

     
    </AppBar>
  );
}

export default Navabr;
