import { lazy } from "react";
import { Routes, Route } from "react-router-dom";

import Navbar from "./pages/Navbar";

import { PatientProvider } from "./contexts/patientContext";
import Dashboard from "./pages/Dashboard";

const Home = lazy(() => import("./pages/Home"));
const Patient = lazy(() => import("./pages/Patient"));
const MyModal = lazy(() => import("./pages/Visit"));
const Test = lazy(() => import("./pages/test"));
const ListPatients = lazy(() => import("./pages/ListPatients"));
const PatientInfo = lazy(() => import("./pages/PatientInfo"));

function App() {
  return (
    <>
      <Navbar />

      <PatientProvider>
        <Routes>
          <Route path="/" element={<Home />}>
            <Route path="list" element={<ListPatients />} />
            <Route path="patient" element={<Patient />} />
            <Route path="patientinfo" element={<PatientInfo />} />
            <Route path="visit" element={<MyModal />} />

            <Route path="dashboard" element={<Dashboard />} />
            <Route path="test" element={<Test />} />
          </Route>

          {/* <Route path="/step1" element={<Step1 />} /> */}
        </Routes>
      </PatientProvider>
    </>
  );
}

export default App;
